import { useState, useEffect, useContext, useCallback } from "react";
import Template from "@/Components/GraphAndChartTemp/Template";
import { getTemplateData } from "@/Pages/Elderlies/Components/Utiles/utiles";
import { numberData } from "./mockData";
import BarChart from "@/Components/GraphAndChart/barChart";
import { CustomContext } from "@/Context/UseCustomContext";
import dayjs from "dayjs";
import { getSleepEvents } from "@/api/deviceReports";

export default function HeartRateAnnomalyStatistic({ isSummaryBtn = true }) {
  const { title, color, icon, dataAnalysis, summaryProps } = getTemplateData(
    "Heart Rate Anomaly Statistic"
  );
  const context = useContext(CustomContext);
  const {
    chartToDate = "",
    chartFromDate = "",
    elderlyDetails,
  } = context || {};
  const [toDate, setToDate] = useState(
    dayjs().subtract(1, "day").format("YYYY-MM-DD")
  );
  const [fromDate, setFromDate] = useState(
    dayjs().subtract(31, "day").format("YYYY-MM-DD")
  );
  const [heartRate, setHeartRate] = useState([]);
  const getHeartRateAnomaly = useCallback(() => {
    getSleepEvents({
      uids: elderlyDetails?.deviceId,
      elderly_id: elderlyDetails._id,
      to_date: fromDate,
      from_date: toDate,
    })
      .then((res) => {
        console.log(res?.data);
        filteredData(res?.data);
        setHeartRate(filteredData(res?.data));
      })
      .catch((err) => {
        console.log(err);
      });
  }, [toDate, elderlyDetails]);

  useEffect(() => {
    getHeartRateAnomaly();
  }, [getHeartRateAnomaly]);

  useEffect(() => {
    if (chartToDate && chartFromDate) {
      setToDate(chartToDate);
      setFromDate(chartFromDate);
    }
  }, [chartToDate]);
  function filteredData(data) {
    // Initialize an empty array to store the results
    const result = [];
    data?.forEach((item) => {
      const eventDate = item.date;
      item?.events?.forEach((ev) => {
        if (ev.event_type === 14 || ev.event_type === 15) {
          // Check if the event date already exists in the result array
          const existingEntry = result.find(
            (entry) => entry.date === eventDate
          );

          if (existingEntry) {
            // If the date exists, increment the total event count for that date
            existingEntry.anomalyHeartRate += 1;
          } else {
            // If the date doesn't exist, create a new entry for that date
            result.push({ date: eventDate, anomalyHeartRate: 1 });
          }
        }
      });
    });
    return result;
  }

  return (
    <Template
      title={title}
      color={color}
      icon={icon}
      dataAnalysis={dataAnalysis}
      isSummaryBtn={isSummaryBtn}
      summaryProps={summaryProps}
    >
      <BarChart
        data={heartRate}
        color={color}
        dataType="number"
        fromDate={fromDate}
        toDate={toDate}
        chartFor="heartRateAnomaly"
      />
    </Template>
  );
}
